import React, { useEffect, useState, useRef } from 'react'
import TemplateOne from "./TemplateOne";
import TemplateTwo from "./TemplateTwo";
import TemplateThree from "./TemplateThree";
import TemplateFour from './TemplateFour';
import TemplateFive from './TemplateFive';

import { useParams } from "react-router-dom";

import axios from "axios";

function TemplateHome() {

    const [funnelID, setFunnelID] = useState("")
    const [designID, setDesignID] = useState("")

    let { id } = useParams();

    function dosom() {
        if (id !== undefined) {
            setFunnelID(id);
        } else {
            console.log("Nope");
        }
    }
    
    useEffect(() => {
        dosom();
    }, []);

    useEffect(() => {
        if (funnelID !== "") {
          getInfo(funnelID)
        }
    }, [funnelID]);
    
    async function getInfo(id) {
        try {
          const res = await axios.get(`https://funnel.dropi.co/api/findByID/${id}`)
          const template = res.data;
          setDesignID(template.DesignID)
          console.log(template);
          return(res.data)
        } catch (error) {
          console.log(error);
        }
    }

  return (
    <div>
        {
        designID === 101 && <TemplateOne/> 
        } 
        {
        designID === 102 && <TemplateTwo/> 
        }
        {
        designID === 103 && <TemplateThree/> 
        }
        {
        designID === 104 && <TemplateFour/> 
        }
        {
        designID === 105 && <TemplateFive/> 
        }
    </div>
  )
}

export default TemplateHome
